import React from 'react'
import logo from './img_Hercules/logo_Hercules.png'
import { useNavigate } from 'react-router-dom'

const HerculesShort = () => {

    const navigate = useNavigate();

    return (
        <div className='shortArticle'>
            <div className='logoContainer'>
                <img id='logoBright' src={logo} alt={"rdo: https://gamesdb.launchbox-app.com/games/images/10245"} title={"Tak wyglda logo gry"}></img>
            </div>
            <div>
                <div>
                    <p lang='pl' id='wstep'>Nie kademu twrcy udaje si przenie film do wiata gry, do tego stopnia, e jest ona mile wspominana do dzisiaj. Jak wyglda sprawa z gr Herkules od Disneya? Postaram si podsun wam odpowied na to pytanie w tej recenzji.</p>
                </div>
                <div id='goToFullArticleButton' onClick={() => navigate(`/artykuly/hercules`)}>Przejd do Artykuu</div>
            </div>
        </div>
    )
}

export default HerculesShort