import React from 'react'
import ButtonGoToTop from '../../components/buttomGoToTop'
import screen1 from './img_WRCArcade/01.jpg'
import screen2 from './img_WRCArcade/02.jpg'
import screen3 from './img_WRCArcade/03.jpg'
import screen4 from './img_WRCArcade/04.jpg'
import screen5 from './img_WRCArcade/05.jpg'
import screen6 from './img_WRCArcade/06.jpg'
import logo from './img_WRCArcade/logo_WRCArcade.png'

const WRCArcadePage = () => {

    return (
        <div>
            <div className='artykul'>
                <img className='logoBright' src={logo} alt={"rdo: wasnorcznie obrysowywaem"} title={"Tak wyglda logo gry"}></img>
                <p lang='pl'>Znaczna cz graczy wychowanych na PS1, w chwili gdy zapytana zostanie o ciekaw gr rajdow odpowie zapewne Colin McRae Rally!.</p>
                <p lang='pl'>Ci lubicy bardziej rozgrywk arcade, wymieni V-Rally lub Rally Masters, ale mao kto zna tytu WRC Arcade, bdcy spin-offem serii o tym samym, trzyliterowym tytule.</p>
                <img className='image' src={screen1} alt={"rdo: wasne"} title={"Subaru Impreza na ulicach jedzie bokiem lepiej jak golf na rcznym"}></img>
                <p lang='pl'>Arcade w nazwie oznacza, e sterowanie jest w niej zrcznociowe. Na szczcie jest ono przyjemne na tyle, e do szybko si do niego mona przyzwyczai. Rozgrywk umila bardzo energiczna muzyka, ktra nastraja do cigania si z przeciwnikami - tak, tu przyjdzie nam si ciga z czterema zawodnikami na danym odcinku, a nie typowo jak w rajdach, poprzez ciganie si z ich wynikiem czasowym.</p>
                <img className='image' src={screen2} alt={"rdo: wasne"} title={"Ford Focus bdzie potrzebowa myjni po takim offroad-zie"}></img>
                <p lang='pl'> Do dyspozycji mamy siedem samochodw rajdowych, biorcych udzia w mistrzostwach z 2002 roku, ktrymi zwiedzimy odcinki a 14-tu krajw takich jak: mrona i biaa Szwajcaria, piaszczysta Australia czy botnista i mokra Wielka Brytania. Wszystko to ubrane jest jeszcze w cztery tryby rozgrywki: Pojedynek z innym graczem, prby czasowe, pojedyncze zawody jak i seria zawodw. Najciekawsze oczywicie dla jednego gracza s te dwa ostatnie, z tym e maj kilka znaczcych rnic.</p>
                <img className='image' src={screen3} alt={"rdo: wasne"} title={"Nie ma to jak jazda bokiem na niegu pikny Focusem"}></img>
                <p lang='pl'>Seria zawodw to po kolei odblokowywane mistrzostwa 3, 6 i 8-mio rundowe, w ktrych na pojedynczej trasie mamy wyprzedzi dwch przeciwnikw. W kadej kolejnej rundzie musimy pokona nastpn dwjk, eby na kocu wyprzedzi lidera. Ciekawe rozwizanie, z ktrym si jeszcze nie spotkaem w grach tego typu. Oczywicie kada trasa jest podzielona na cztery checkpointy, ktre dodaj kilkadziesit sekund do limitu czasu.</p>
                <img className='image' src={screen4} alt={"rdo: wasne"} title={"Jak nie sta nas na myjnie, mona wykorzysta naturalne zasoby"}></img>
                <p lang='pl'>Co innego dzieje si w serii pojedynczych zawodw, gdzie mamy matryc aut i tras i musimy wygra w kadej moliwej konfiguracji. Zwycistwo daje konkretn liczb punktw, gdzie za pierwsze miejsce dostajemy trzy punkty i po uzbieraniu konkretnej liczby, odblokowuj si kolejne zawody z matrycy. W tych zawodach, w przeciwiestwie do serii, walczymy z czwrk aut, a z kolejnymi odblokowanymi zawodami, przeciwnicy s coraz trudniejsi do pokonania chocia niezupenie...</p>
                <img className='image' src={screen5} alt={"rdo: Wasne"} title={"Mitsubishi Lancer najlepiej sprawdza si na piaszczystych drogach"}></img>
                <p lang='pl'>Gra niestety cierpi mocno na brak wyzwania, gdy przeszedem mistrzostwa, nie tracc adnego kredytu (mamy 3 na mistrzostwa), a wikszo zawodw wygrywaem za pierwszym razem. Jest to rozczarowujce gdy przez to tytu staje si bardzo krtki, bo mona go ukoczy na 100% w okoo 5 godzin. Smutne jest te to, e dopiero ostatnie zawody z matrycy daj jakie wyzwanie i trzeba si w nich naprawd namczy, eby zdoby zoto. Kolejnym utrudnieniem w grze jest fizyka, ktra jest tutaj bardzo zabawna. Auta potrafi podskakiwa jakby byy gumowymi pikami, a uderzenia w barierki s bardzo mikkie aczkolwiek mocno hamuj pojazd.</p>
                <img className='image' src={screen6} alt={"rdo: Wasne"} title={"Na kocu i tak zdobdziemy nasze upragnione zoto"}></img>
                <p lang='pl'>Graficznie omawiana produkcja przedstawia si przyzwoicie, auta s na tyle odwzorowane, e mona je rozpozna bez problemu. wiat rwnie jest bardzo sympatyczny i pokazuje co konsola potrafi. Czasami jednak pojawi si pewne problemy, jak na przykad obiekty wiszce w powietrzu, ale jest to rzadko i trzeba chcie to wychwyci. To co mi si tutaj podoba, to efekt brudzenia si auta i cho nie jest on tak efektowny jak w pierwszym Colinie, to boto, ziemia i piach powoduj, e auto si brudzi przy pomocy przyciemnienia, a wjazd w kau sukcesywnie to wszystko zmywa.</p>
                <p lang='pl'>WRC Arcade uwaam za bardzo dobr gr rajdow z gatunku zrcznociowych, bardzo pozytywnie mnie zaskoczya i jak zaczem w ni gra, to ukoczyem na 100%. Robiem to z umiechem na twarzy i ze szczer radoci, a wiedzcie e nie jestem zwolennikiem arcadowego rodzaju rozgrywki. Jak bdziecie mieli okazj ogra ten tytu, to dajcie mu szans, nie poaujecie.</p>
                <div className='tabela'>
                    <table className='tabelaInfo'>
                        <tr className='tabelaInfo'>
                            <td>Deweloper</td>
                            <td>Unique Dev. Studios</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Wydawca</td>
                            <td>Sony</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Data wydania</td>
                            <td>8 listopada 2002</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Numer Seryjny</td>
                            <td>SCES-00891</td>
                        </tr>
                    </table>
                </div>
            </div>
            <ButtonGoToTop />
        </div>
    )
}

export default WRCArcadePage