import React from 'react'
import logo from './img_WRCArcade/logo_WRCArcade.png'
import { useNavigate } from 'react-router-dom'

const WRCArcadeShort = () => {

    const navigate = useNavigate();

    return (
        <div className='shortArticle'>
            <div className='logoContainer'>
                <img id='logoBright' src={logo} alt={"rdo: wasnorcznie obrysowywaem"} title={"Tak wyglda logo gry"}></img>
            </div>
            <div>
                <div>
                    <p lang='pl' id='wstep'>Znaczna cz graczy wychowanych na PS1, w chwili gdy zapytana zostanie o ciekaw gr rajdow odpowie zapewne Colin McRae Rally!.</p>
                </div>
                <div id='goToFullArticleButton' onClick={() => navigate(`/artykuly/wrcarcade`)}>Przejd do Artykuu</div>
            </div>
        </div>
    )
}

export default WRCArcadeShort