import React from 'react'
import ButtonGoToTop from '../../components/buttomGoToTop'
import screen1 from './img_Hercules/01.jpg'
import screen2 from './img_Hercules/02.jpg'
import screen3 from './img_Hercules/03.jpg'
import screen4 from './img_Hercules/04.jpg'
import screen5 from './img_Hercules/06.jpg'
import screen6 from './img_Hercules/07.jpg'
import screen7 from './img_Hercules/08.jpg'
import logo from './img_Hercules/logo_Hercules.png'

const HerculesPage = () => {

    return (
        <div>
            <div className='artykul'>
                <img className='logoBright' src={logo} alt={"rdo: https://gamesdb.launchbox-app.com/games/images/10245"} title={"Tak wyglda logo gry"}></img>
                <p lang='pl'>Nie kademu twrcy udaje si przenie film do wiata gry, do tego stopnia, e jest ona mile wspominana do dzisiaj. Jak wyglda sprawa z gr Herkules od Disneya? Postaram si podsun wam odpowied na to pytanie w tej recenzji.</p>
                <img className='image' src={screen1} alt={"rdo: wasne"} title={"Plansza treningowa"}></img>
                <p lang='pl'>Pierwsze co si nam rzuca w oczy to bardzo adna grafika 2.5D, ktra jest poczeniem plansz, obiektw w trjwymiarze i postaci jako  paskich obiektw. Przyjdzie nam zwiedza <abbr title='Moemy przenosi si w gb planszy lub przej "bliej" ekranu'>wielowarstwowe</abbr> kolorowe lasy, wielopitrowe zaludnione miasto, lata w przestworzach na wiernym <abbr title='Mityczny ko ze skrzydami'>pegazie</abbr>, jak rwnie pdzi w gb ekranu gonic za szczciem, <abbr title='Mityczny stwr przypominajcy czowieka, ale z charakterystyczny jednym okiem'>cyklopem</abbr> czy za gwnym zolem.</p>
                <img className='image' src={screen2} alt={"rdo: wasne"} title={"Plansza w lesie, pikna, wielowarstwowa i niebezpieczna"}></img>
                <p lang='pl'>W trakcie naszych poczyna towarzyszy bdzie nam przyjemna muzyka, ktrej rne tony bd przygrywa w zalenoci od danej sytuacji. Za przerywniki filmowe su tu klipy z filmowego, Disney-owego Herkulesa, ktre ujawniaj histori przed jak i po ukoczonym poziomie. S one w bardzo dobrej jakoci i prawdopodobnie zajmuj wiksz cz pyty.</p>
                <img className='image' src={screen3} alt={"rdo: wasne"} title={"Te mae futrzaki przeraay mnie za modego jak i dzisiaj"}></img>
                <p lang='pl'>Jak wspomniaem wczeniej, na pocztku bdziemy mieli plansz treningow, gdzie Herkules poznaje swoje umiejtnoci. W lesie zwiedzimy jego pierwszy, drugi, a nawet trzeci plan. W piknym miecie odwiedzimy labirynt piter i zakamarkw z <abbr title='Gdzie mona znale litery oraz naczynia do zrobienia save-a'>sekretami</abbr>. W midzy czasie jeszcze pobiegamy troch po torach przeszkd, ulicach miasta czy po wiecie umarych. Jak gra z pocztku jest kolorowa tak z czasem ustpuje bardziej stonowanym, mrocznym kolorom, e pod koniec mamy do czynienia z gr <abbr title='Gra robi si straszna, ale w bardzo agodny sposb'>soft-koszmar</abbr>.</p>
                <img className='image' src={screen4} alt={"rdo: wasne"} title={"Pierwszy wikszy boss, przeraajcy ale klimatyczny"}></img>
                <p lang='pl'>Z czasem rodzaj rozrywki rwnie si zmienia. Plansze platformowe ustpuj tym ganianym, eby pniej przeksztaci si tylko w walki z <abbr title='Duzi i mocniejsi przeciwnicy w sbie gownego zego'>bossami</abbr>. Tych mamy kilkoro: <abbr title='P-czowiek, p-ko'>Centaur</abbr>, <abbr title='Czowiek z gow byka'>Minotaur</abbr>,  stado porywajcych ludzi ptaszysk, <abbr title='Smok ktremu z odcitej gowy wyrastaj kolejne'>Hydra</abbr>, <abbr title='Kobieta z wami zamiast wosw, ktra zamienia wszystkie ywe stworzenia w kamie'>Meduza</abbr> i gwny zy  <abbr title='Bg zawiatw w mitologii greckiej'>Hades</abbr>.</p>
                <p lang='pl'>Teraz powiem Wam co, czego wikszo graczy nie jest wiadoma, gra jest BARDZO krtka, ukoczyem j zaledwie w godzin. Przez ca t godzin straciem moe z pi y (wikszo na przedostatniej planszy). Walki z bossami s banalnie proste dla przecitnego gracza, a ostatnia walka pokazuje e twrcom zabrako pomysw.</p>
                <img className='image' src={screen5} alt={"rdo: Wasne"} title={"Plansze biegankowe, s tutaj czstrze, a im dalej w las tym jest trudniej"}></img>
                <p lang='pl'>Kolejnym problemem jest tutaj w pewnym stopniu sterowanie naszym herosem. Potny Herkules nie radzi sobie niestety z otrzymywaniem obrae, gdy po kadym trafieniu pojawia si sekundowa animacja ktra unieruchamia posta lub przerywa wykonywan akcj.</p>
                <img className='image' src={screen6} alt={"rdo: Wasne"} title={"Jedyna taka plansza w grze - do tego najtrudniejsza"}></img>
                <p lang='pl'>Ostatnim gwodziem do trumny jest system znajdziek, ktre tutaj nie maj adnego sensu. Zbieramy litery ktre skadaj si w imi naszego bohatera, oraz specjalne wazy. Te ostatnie daj nam moliwo zapisania stanu gry, a peny napis z tego co rozumiem odblokowuje moliwo ponownego uruchomienia planszy.</p>
                <img className='image' src={screen7} alt={"rdo: Wasne"} title={"Przedostatnia plansza, najmroczniejsza i trudna"}></img>
                <p>Nostalgia do tej gry u mnie niestety przemina i nie bdzie ju t, ktr zapamitaem z dziecistwa. Nie jest ona tak duga jak zapamitaem, nie jest ona rwnie tak trudna jak utkwia mi w pamici. Graficznie do dzisiaj trzyma poziom, ale jeeli chodzi o sterowanie, to wiek jej nie oszczdza. Myl e pomogem Wam na odpowied na pytanie z pocztku tego tekstu.</p>
                <div className='tabela'>
                    <table className='tabelaInfo'>
                        <tr className='tabelaInfo'>
                            <td>Deweloper</td>
                            <td>Eurocom</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Wydawca</td>
                            <td>Disney Int.</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Data wydania</td>
                            <td>15 listopada 1998</td>
                        </tr>
                        <tr className='tabelaInfo'>
                            <td>Numer Seryjny</td>
                            <td>SCES-00819</td>
                        </tr>
                    </table>
                </div>
            </div>
            <ButtonGoToTop />
        </div>
    )
}

export default HerculesPage